import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
OtherInt.propTypes = {
  discount: PropTypes.object,
};
export default function OtherInt({ discount }) {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
  });
  const defaultValues = useMemo(
    () => ({
      simserver: discount?.simserver,
      simhosting: discount?.simhosting,
      smeplug: discount?.smeplug,
      msplug: discount?.msplug,
      ogdamns: discount?.ogdamns,
      vtpass_username: discount?.vtpass_username,
      vtpass_password: discount?.vtpass_password,
      hollatag_username: discount?.hollatag_username,
      hollatag_password: discount?.hollatag_password,
      easy_access: discount?.easy_access,
      boltnet_token: discount?.boltnet_token,
      zimrax: discount?.zimrax,
      hamdala: discount?.hamdala,
      autopilot_key: discount?.autopilot_key
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/otherapi/charges/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Updated`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="simserver" type="password" autoComplete="new-password" label="SIM SERVER API KEY" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="simhosting" type="password" autoComplete="new-password" label="SIM HOSTING API KEY" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="ogdamns" type="password" autoComplete="new-password" label="OGDAMNS API KEY" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="smeplug" type="password" autoComplete="new-password" label="SMEPLUG API KEY" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="msplug" type="password" autoComplete="new-password" label="MSPLUG API KEY" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="vtpass_username" label="VTPASS USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="vtpass_password" type="password" autoComplete="new-password" label="VTPASS PASSWORD" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="hollatag_username" label="HOLLATAG USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="hollatag_password" type="password" autoComplete="new-password" label="HOLLATAG PASSWORD" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="easy_access" type="password" autoComplete="new-password" label="Easy Access Token" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="boltnet_token" type="password" autoComplete="new-password" label="BoltNet API Token" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="zimrax" type="password" autoComplete="new-password" label="Zimrax API Token" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="hamdala" type="password" autoComplete="new-password" label="Hamdala API Token" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="autopilot_key" type="password" autoComplete="new-password" label="Autopilot API Token" InputLabelProps={{ shrink: true }} />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
